import React from "react";
import "./index.less";
import PropTypes from "prop-types";

const CenteredBlock = (props) => (
  <div className="centered-block-container" onKeyPress={props.handlePressKey}>
    <div className="centered-block-form">
      <div className="centered-block-header">
        <div className={"centered-block-header-title"}>{props.title}</div>
      </div>
      <div className="centered-block-body">{props.children}</div>
    </div>
  </div>
);

CenteredBlock.propTypes = {
  handlePressKey: PropTypes.any,
  children: PropTypes.any,
  title: PropTypes.any,
};

export default CenteredBlock;
