import { BuildAPIUrl, URLS, URLS_LOCAL } from "../../utils/config/URLS";
import { toast } from "react-toastify";
import { handleRespErr } from "../../helpers/respErrorsHandler";
import { GetAxios } from "../../helpers/axios";
import {
  onSetNewAdminPasswordFailure,
  onSetNewAdminPasswordSuccess,
  onSetNewPasswordFailure,
  onSetNewPasswordSuccess,
  setUserAuthToken,
  UpdateAuthToken,
  User2FaChangeStatusActionFailure,
  User2FaChangeStatusActionRequest,
  User2FaChangeStatusActionSuccess,
  User2FaGenerateSecretActionFailure,
  User2FaGenerateSecretActionRequest,
  User2FaGenerateSecretActionSuccess,
  User2FaOptVerifyActionFailure,
  UserSendResetEmailActionFailure,
  UserSendResetEmailActionRequest,
  UserSignInActionFailure,
  UserSignInActionRequest,
  UserSignInActionSuccess,
  userSignOutAction,
} from "../../store/actions/userActions";

const axios = GetAxios();

export function onSetNewPassword(adminData, router) {
  return (dispatch) => {
    axios
      .post(BuildAPIUrl(URLS.SET_NEW_PASSWORD), adminData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        dispatch(onSetNewPasswordSuccess());
        toast.success("Password was changed");
        router.push(URLS_LOCAL.SIGN_IN);
      })
      .catch(handleRespErr(dispatch, onSetNewPasswordFailure));
  };
}

export function onSetNewAdminPassword(adminData, router) {
  return (dispatch) => {
    axios
      .post(BuildAPIUrl(URLS.SET_NEW_ADMIN_PASSWORD), adminData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        dispatch(onSetNewAdminPasswordSuccess());
        toast.success("Password was changed");
        router(URLS_LOCAL.SIGN_IN);
      })
      .catch(handleRespErr(dispatch, onSetNewAdminPasswordFailure));
  };
}

export function User2FaOtpVerifyAction(otp) {
  return (dispatch) => {
    axios
      .post(
        BuildAPIUrl(URLS.OTP_VERIFY),
        {
          otp,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => {
        dispatch(
          UserSignInActionSuccess({
            accessToken: res.data.token,
            user: res.data.admin,
            mfaEnabled: res.data.mfaEnabled,
            isAuthorized: res.data.isAuthorized,
          })
        );
        dispatch(UpdateAuthToken());
      })
      .catch((res) => {
        if (!res.response.data.error) {
          try {
            toast.success("success");
          } catch (e) {
            console.log(e);
          }
        } else {
          try {
            let resData = {};
            res.response.data.error.forEach((el) => {
              resData[el.name] = el.message;
            });
            dispatch(User2FaOptVerifyActionFailure(resData));
          } catch (e) {
            console.log(e);
          }
        }
      });
  };
}

export function User2FaChangeStatusAction(
  otp,
  mfaEnabled,
  successCallback,
  failedCallback
) {
  return (dispatch) => {
    dispatch(User2FaChangeStatusActionRequest());
    axios
      .put(
        BuildAPIUrl(URLS.TWO_FA_CHANGE_STATUS),
        {
          otp,
          enableTwoFa: mfaEnabled,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => {
        dispatch(User2FaChangeStatusActionSuccess(res.data));
        dispatch(userSignOutAction());
        successCallback(res.data.message || "");
      })
      .catch((res) => {
        dispatch(User2FaChangeStatusActionFailure(res.message));
        try {
          failedCallback(res.data.message);
        } catch (e) {
          failedCallback(res.message);
        }
      });
  };
}

export function User2FaGenerateSecretAction() {
  return (dispatch) => {
    dispatch(User2FaGenerateSecretActionRequest());
    axios
      .post(
        BuildAPIUrl(URLS.TWO_FA_GENERATE_SECRET),
        {},
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => {
        dispatch(User2FaGenerateSecretActionSuccess(res.data));
      })
      .catch((res) => {
        dispatch(User2FaGenerateSecretActionFailure(res.message));
      });
  };
}

export function UserSendResetEmailAction(email) {
  return (dispatch) => {
    dispatch(UserSendResetEmailActionRequest());
    const userData = {
      email: email,
    };

    axios
      .post(BuildAPIUrl("/api/password/send-reset-email"), userData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        toast.success("success");
      })
      .catch(handleRespErr(dispatch, UserSendResetEmailActionFailure));
  };
}

export function UserRefreshTokenAction(token) {
  return (dispatch) => {
    const userData = {
      token: "Bearer " + token,
    };

    axios
      .post(BuildAPIUrl(URLS.REFRESH_TOKEN), userData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        dispatch(UpdateAuthToken());
        dispatch(setUserAuthToken(res.data));
      })
      .catch((res) => {
        if (res && res.response && res.response.status === 401) {
          dispatch(userSignOutAction());
        }
      });
  };
}

export function UserSignInAction(email, password, routerHistory) {
  return (dispatch) => {
    dispatch(UserSignInActionRequest());
    const userData = {
      loginOrEmail: email,
      password: password,
    };
    axios
      .post(BuildAPIUrl(URLS.SIGN_IN), userData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((res) => {
        if (res.data.mfaEnabled) {
          dispatch(
            UserSignInActionSuccess({
              accessToken: res.data.token,
              user: {},
              mfaEnabled: true,
              isAuthorized: res.data.isAuthorized,
            })
          );
          dispatch(UpdateAuthToken());
          routerHistory(URLS_LOCAL.TWO_FACTOR);
        } else {
          dispatch(
            UserSignInActionSuccess({
              accessToken: res.data.token,
              user: res.data.user,
              mfaEnabled: res.data.mfaEnabled,
              isAuthorized: res.data.isAuthorized,
            })
          );
          dispatch(UpdateAuthToken());
        }
      })
      .catch(handleRespErr(dispatch, UserSignInActionFailure));
  };
}
