import { displayRequestErrors } from "./displayRequestErrors";
import { toast } from "react-toastify";

export function handleRespErr(dispatch, action) {
  return (res) => {
    const { hasValidationErr, msg } = responseErrorMsg(res);
    dispatch(action(msg));
    if (hasValidationErr) {
      displayRequestErrors(hasValidationErr);
    } else {
      toast.error(msg);
    }
  };
}
function responseErrorMsg(res) {
  if (res.response) {
    return {
      hasValidationErr:
        (res && res.response && res.response.data && res.response.data.error) ||
        false,
      msg:
        (res && res.response && res.response.data && res.response.data.error) ||
        res.response.data.message ||
        res.message ||
        "Something went wrong",
    };
  } else {
    return {
      hasValidationErr: false,
      msg: "Network error",
    };
  }
}
