import * as actions from "../../actions/manualBankingActions/constants";

const initialState = {
  deposits: {
    newDeposits: {
      data: [],
      pages: "",
      total: "",
    },
    pendingDeposits: {
      data: [],
      pages: "",
      total: "",
    },
    rejectedDeposits: {
      data: [],
      pages: "",
      total: "",
    },
    approvedDeposits: {
      data: [],
      pages: "",
      total: "",
    },
  },
  withdrawals: {
    withdrawalsRequests: {
      data: [],
      pages: "",
      total: "",
    },
    withdrawalsHistory: {
      data: [],
      pages: "",
      total: "",
    },
    abaFiles: {
      data: [],
      pages: "",
      total: "",
    },
    abaArchiveFiles: {
      data: [],
      pages: "",
      total: "",
    },
    currentAbaFile: {
      data: [],
      pages: "",
      total: "",
    },
  },
  bankStatements: {
    data: [],
    pages: "",
    total: "",
  },
  csvFiles: {
    data: [],
    pages: "",
    total: "",
  },
  adminAccount: {
    csvBankStatements: {
      data: [],
      pages: "",
      total: "",
    },
    requests: {
      data: [],
      pages: "",
      total: "",
    },
  },
  archive: {
    data: [],
    pages: "",
    total: "",
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actions.ADMIN_GET_NEW_DEPOSITS_SUCCESS:
      return {
        ...state,
        deposits: {
          ...state.deposits,
          newDeposits: action.payload,
        },
      };
    case actions.ADMIN_GET_PENDING_DEPOSITS_SUCCESS:
      return {
        ...state,
        deposits: {
          ...state.deposits,
          pendingDeposits: action.payload,
        },
      };
    case actions.ADMIN_GET_REJECTED_DEPOSITS_SUCCESS:
      return {
        ...state,
        deposits: {
          ...state.deposits,
          rejectedDeposits: action.payload,
        },
      };
    case actions.ADMIN_GET_APPROVED_DEPOSITS_SUCCESS:
      return {
        ...state,
        deposits: {
          ...state.deposits,
          approvedDeposits: action.payload,
        },
      };
    case actions.ADMIN_GET_ABA_FILES_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...state.withdrawals,
          abaFiles: action.payload,
        },
      };
    case actions.ADMIN_GET_ARCHIVE_ABA_FILES_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...state.withdrawals,
          abaArchiveFiles: action.payload,
        },
      };
    case actions.ADMIN_GET_ABA_FILE_BY_ID_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...state.withdrawals,
          currentAbaFile: action.payload,
        },
      };
    case actions.ADMIN_GET_WITHDRAWALS_REQUESTS_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...state.withdrawals,
          withdrawalsRequests: action.payload,
        },
      };
    case actions.ADMIN_GET_WITHDRAWALS_HISTORY_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...state.withdrawals,
          withdrawalsHistory: action.payload,
        },
      };
    case actions.ADMIN_GET_BANK_STATEMENTS_SUCCESS:
      return {
        ...state,
        bankStatements: action.payload,
      };
    case actions.ADMIN_GET_CSV_FILES_SUCCESS:
      return {
        ...state,
        csvFiles: action.payload,
      };
    case actions.ADMIN_GET_CSV_BANK_STATEMENTS_SUCCESS:
      return {
        ...state,
        adminAccount: {
          ...state.adminAccount,
          csvBankStatements: action.payload,
        },
      };
    case actions.ADMIN_GET_ADMIN_ACCOUNT_DEPOSITS_REQUEST_SUCCESS:
      return {
        ...state,
        adminAccount: {
          ...state.adminAccount,
          requests: action.payload,
        },
      };
    case actions.ADMIN_GET_ARCHIVE_SUCCESS:
      return {
        ...state,
        archive: action.payload,
      };
    default:
      return state;
  }
};
