/**
 * @return {string}
 */
export function BuildAPIUrl(url) {
  if (URL_PORT === 80 || URL_PORT === 443) {
    return URL_FULL + url;
  }

  return URL_FULL + ":" + URL_PORT + url;
}

/**
 * @param url
 * @returns {string}
 * @constructor
 */
export function BuildAPIUrlWs(url) {
  if (URL_PORT === 80 || URL_PORT === 443) {
    return URL_FULL_WS + url;
  }

  return URL_FULL_WS + ":" + URL_PORT + url;
}
