import { applyMiddleware, compose, createStore } from "redux";
import { composeWithDevTools } from "redux-devtools-extension";
import thunkMiddleware from "redux-thunk";
import { persistStore } from "redux-persist";
import {
  loadTranslations,
  setLocale,
  syncTranslationWithStore,
} from "react-redux-i18n";

import reducers from "./reducers";
import translation from "./i18n";
import { debug } from "../utils/config";
import { UpdateAuthToken } from "./actions/userActions";
import { UserRefreshTokenAction } from "../requests/userRequests";

function middleware() {
  if (debug) {
    return [thunkMiddleware];
  } else {
    return [thunkMiddleware];
  }
}

let middlewares;
if (debug) {
  middlewares = composeWithDevTools(applyMiddleware(...middleware()));
} else {
  middlewares = compose(applyMiddleware(...middleware()));
}

export const store = createStore(reducers, undefined, middlewares);

store.dispatch(loadTranslations(translation));
store.dispatch(setLocale("en"));
syncTranslationWithStore(store);

export const persistor = persistStore(store).subscribe(() => {
  store.dispatch(UpdateAuthToken());

  setInterval(() => {
    store.dispatch(UserRefreshTokenAction(store.getState().user.accessToken));
  }, 60 * 30 * 1000);
});
