import * as profileActions from "../../actions/adminActions/constants";

const initialState = {
  profile: {
    profile: {
      email: "",
      fullName: "",
      country: "",
      mobileNumber: "",
      dateOfBirth: "",
    },
    documents: {
      isLoading: true,
    },
    companyDetails: {
      isLoading: true,
    },
    superFundDetails: {
      isLoading: true,
    },
    summary: {
      type: {
        value: "",
        comment: "",
        status: {
          id: 0,
          name: "",
        },
      },
      sourceOfFunds: {
        value: "",
        comment: "",
        status: {
          id: 0,
          name: "",
        },
      },
      purpose: {
        value: "",
        comment: "",
        status: {
          id: 0,
          name: "",
        },
      },
      status: {
        id: 0,
        name: "",
      },
      isLoading: true,
    },
    address: {
      isLoading: true,
    },
    identityDetails: {
      isLoading: true,
    },
    isLoading: true,
  },
  balances: {
    data: [],
    isLoading: true,
  },
  orders: {
    data: [],
    isLoading: true,
  },
  trades: {
    data: [],
    isLoading: true,
  },
  withdrawals: {
    data: [],
  },
  deposits: {
    data: [],
    isLoading: true,
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case profileActions.PROFILE_GET_ACCOUNT_SUMMARY_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          summary: {
            ...action.payload,
            isLoading: false,
          },
        },
      };
    case profileActions.PROFILE_UPDATE_ACCOUNT_SUMMARY_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          summary: {
            ...action.payload,
            isLoading: false,
          },
        },
      };
    case profileActions.PROFILE_GET_ADDRESS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          address: action.payload,
        },
      };
    case profileActions.PROFILE_UPDATE_ADDRESS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          address: action.payload,
        },
      };
    case profileActions.PROFILE_SEND_DOCUMENT_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          documents: action.payload,
        },
      };
    case profileActions.PROFILE_GET_DOCUMENT_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          documents: {
            ...action.payload,
            isLoading: false,
          },
        },
      };
    case profileActions.PROFILE_GET_IDENTITY_DETAILS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          identityDetails: action.payload,
        },
      };
    case profileActions.PROFILE_GET_BANK_DETAILS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          bankDetails: action.payload,
        },
      };
    case profileActions.PROFILE_GET_COMPANY_DETAILS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          companyDetails: {
            ...state.profile.companyDetails,
            isLoading: false,
            details: action.payload,
          },
        },
      };
    case profileActions.PROFILE_ADD_DIRECTOR_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          companyDetails: {
            ...state.profile.companyDetails,
            details: {
              ...state.profile.companyDetails.details,
              directors: action.payload,
            },
          },
        },
      };
    case profileActions.PROFILE_GET_SUPER_FUND_DETAILS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          superFundDetails: {
            ...state.profile.superFundDetails,
            isLoading: false,
            details: action.payload,
          },
        },
      };
    case profileActions.PROFILE_UPDATE_BANK_DETAILS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          bankDetails: action.payload,
        },
      };
    case profileActions.PROFILE_UPDATE_IDENTITY_DETAILS_SUCCESS:
      return {
        ...state,
        profile: {
          ...state.profile,
          identityDetails: action.payload,
        },
      };
    case profileActions.PROFILE_GET_CLIENT_BALANCES_SUCCESS:
      return {
        ...state,
        balances: {
          data: [...action.payload],
          isLoading: false,
        },
      };
    case profileActions.PROFILE_GET_CLIENT_ORDERS_SUCCESS:
      return {
        ...state,
        orders: {
          ...action.payload,
          isLoading: false,
        },
      };
    case profileActions.PROFILE_GET_CLIENT_TRADES_SUCCESS:
      return {
        ...state,
        trades: {
          ...action.payload,
          isLoading: false,
        },
      };
    case profileActions.PROFILE_GET_CLIENT_WITHDRAWALS_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...action.payload,
          isLoading: false,
        },
      };
    case profileActions.PROFILE_GET_CLIENT_DEPOSITS_SUCCESS:
      return {
        ...state,
        deposits: {
          ...action.payload,
          isLoading: false,
        },
      };
    default:
      return state;
  }
};
