import * as platformBalancesActions from "../../actions/platformBalancesAction/constants";

const initialState = {
  balances: [],
  usersBalancesByCcy: {
    data: [],
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case platformBalancesActions.PLATFORM_GET_BALANCES_SUCCESS:
      return {
        ...state,
        balances: action.payload,
      };
    case platformBalancesActions.PLATFORM_GET_USERS_BALANCES_BY_CCY_SUCCESS:
      return {
        ...state,
        usersBalancesByCcy: {
          ...action.payload,
        },
      };
    default:
      return state;
  }
};
