import * as adminActions from "../../actions/adminActions/constants";

const initialState = {
  meList: [],
};

export default (state = initialState, action) => {
  switch (action.type) {
    case adminActions.ADMIN_GET_FULL_ME_SUCCESS:
      return {
        ...state,
        meList: action.payload,
      };
    default:
      return state;
  }
};
