import * as userActions from "../../actions/userActions/constants";
import { AxiosSetAuth } from "../../../helpers/axios";

const initialState = {
  loggedIn: false,
  messages: {
    email: "",
    password: "",
  },
  theme: "Classic",
  admin: {
    email: "",
    firstName: "",
    lastName: "",
    role: {
      name: "",
    },
  },
  processed: true,
  accessToken: "",
  security: {
    imgBase64: "",
    secret: "",
    otp: "",
    mfaEnabled: false,
    messages: {
      otp: "",
    },
  },
};

/**
 * Application reducer.
 */
export default (state = initialState, action) => {
  switch (action.type) {
    case userActions.ACTION_USER_SIGN_IN_REQUEST:
      return {
        ...state,
        processed: true,
      };
    case userActions.ACTION_USER_SET_TOKEN:
      return {
        ...state,
        accessToken: action.payload.accessToken,
        loggedIn: action.payload.isAuthorized,
      };
    case userActions.ACTION_USER_SIGN_IN_SUCCESS:
      return {
        ...state,
        processed: false,
        loggedIn: action.userData.isAuthorized,
        admin: action.userData.user,
        security: {
          ...state.security,
          mfaEnabled: action.userData.mfaEnabled,
        },
        accessToken: action.userData.accessToken,
      };
    case userActions.ACTION_USER_SIGN_IN_FAILURE:
      return {
        ...state,
        processed: false,
        messages: action.payload.error,
      };
    case userActions.ACTION_USER_SIGN_OUT:
      return initialState;
    case userActions.ACTION_USER_SEND_RESET_EMAIL_REQUEST:
      return {
        ...state,
        processed: true,
      };
    case userActions.ACTION_USER_SEND_RESET_EMAIL_SUCCESS:
      return {
        ...state,
        processed: false,
        messages: action.payload,
      };
    case userActions.ACTION_USER_SEND_RESET_EMAIL_FAILURE:
      return {
        ...state,
        processed: false,
        messages: action.payload.error,
      };
    case userActions.ACTION_RESET_ALL_ERROR_MESSAGE:
      return {
        ...state,
        messages: {},
      };
    case userActions.ACTION_RESET_ERROR_MESSAGE:
      return {
        ...state,
        messages: {
          ...state.messages,
          [action.payload.field]: "",
        },
      };
    case userActions.ACTION_USER_2FA_GENERATE_SECRET_SUCCESS:
      return {
        ...state,
        security: {
          ...state.security,
          imgBase64: action.payload.imgBase64,
          secret: action.payload.secret,
          otp: "",
          mfaEnabled: false,
        },
      };
    case userActions.ACTION_USER_2FA_CHANGE_OTP:
      return {
        ...state,
        security: {
          ...state.security,
          otp: action.payload.otp,
        },
      };
    case userActions.ACTION_USER_2FA_CHANGE_OTP_FAILURE:
      return {
        ...state,
        security: {
          ...state.security,
          messages: action.payload.error,
        },
      };
    case userActions.ACTION_USER_UPDATE_TOKEN:
      AxiosSetAuth(state.accessToken);
      return state;
    default:
      return state;
  }
};
