import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { I18n } from "react-redux-i18n";
import Input from "../Common/Input";
import Button from "../Common/Button";
import "./index.less";
import {
  LOG_IN,
  EMAIL,
  PASSWORD,
  FORGOT_PASSWORD,
} from "../../utils/config/language";
import CenteredBlock from "../Common/CenteredBlock";
import { URLS_LOCAL_GROUPS } from "../../utils/config/URLS";
import { useDispatch, useSelector } from "react-redux";
import {
  onChangeInputData,
  resetAllErrorMessageAction,
  verifyTokenAction,
} from "../../store/actions/userActions";
import { useNavigate } from "react-router";
import { UserSignInAction } from "../../requests/userRequests";

const SignIn = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const loggedIn = useSelector((store) => store.user.loggedIn);
  const messages = useSelector((store) => store.user.messages);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    showPassword: false,
    remember: false,
  });
  useEffect(() => {
    dispatch(verifyTokenAction());
    dispatch(resetAllErrorMessageAction());
    loggedIn && navigate(URLS_LOCAL_GROUPS.clientUser);
  }, [loggedIn]);
  const handleOnChange = (name, value) => {
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    let args = { field: name, value: value };
    dispatch(onChangeInputData(args));
  };
  const handleOnClick = (event) => {
    const { email, password } = formData;
    event.preventDefault();
    if (!messages["password"] && !messages["email"]) {
      dispatch(UserSignInAction(email, password, navigate));
    }
  };
  const { email, password, showPassword } = formData;
  return (
    <CenteredBlock title={I18n.t(`buttons.${LOG_IN}`)}>
      <form onSubmit={(event) => handleOnClick(event)}>
        <Input
          type="email"
          name="email"
          title={`register.${EMAIL}`}
          className=""
          placeholder={`register.${EMAIL}`}
          value={email}
          error={messages["email"]}
          onChange={({ target }) => handleOnChange(target.name, target.value)}
        />
        <div className="input-wrp">
          <Input
            type={showPassword ? "text" : "password"}
            name="password"
            title={`register.${PASSWORD}`}
            className=""
            placeholder={`register.${PASSWORD}`}
            value={password}
            error={messages["password"]}
            onChange={({ target }) => handleOnChange(target.name, target.value)}
          />
        </div>
        <div className="signin-options-wrapper">
          <Link to="/forgot-password">
            {I18n.t(`login.${FORGOT_PASSWORD}`)}
          </Link>
        </div>
        <div className="signin-btn-wrapper">
          <Button
            type="submit"
            className="signin-btn btn-green"
            onClick={(event) => handleOnClick(event)}
          >
            {I18n.t(`buttons.${LOG_IN}`)}
          </Button>
        </div>
      </form>
    </CenteredBlock>
  );
};

export default SignIn;
