import * as actions from "./constants.js";
import { ACTION_USER_UPDATE_TOKEN } from "./constants.js";

export function UserSignInActionSuccess(userData) {
  return {
    type: actions.ACTION_USER_SIGN_IN_SUCCESS,
    userData: {
      accessToken: userData.accessToken,
      user: userData.user,
      mfaEnabled: userData.mfaEnabled,
      isAuthorized: userData.isAuthorized,
    },
  };
}

export function setUserAuthToken(data) {
  return {
    type: actions.ACTION_USER_SET_TOKEN,
    payload: data,
  };
}

export function UpdateAuthToken() {
  return {
    type: ACTION_USER_UPDATE_TOKEN,
  };
}

export function UserSignInActionRequest(status) {
  return {
    type: actions.ACTION_USER_SIGN_IN_REQUEST,
    payload: {
      status: status,
    },
  };
}

export function UserSignInActionFailure(message) {
  return {
    type: actions.ACTION_USER_SIGN_IN_FAILURE,
    payload: {
      error: message,
    },
  };
}

