export function onChangeInputData(args) {
  return (dispatch) => {
    dispatch(resetErrorMessageAction(args.field));
  };
}

export function errorMessageAction(field, message) {
  return {
    type: actions.ACTION_ERROR_MESSAGE,
    payload: {
      field,
      message,
    },
  };
}

