import React, { useState, useRef, useEffect } from "react";
import "./index.less";
import UserOptions from "./userOptions";

const UserPanel = () => {
  const [showUserOptions, setShowUserOptions] = useState(false);
  const myRef = useRef();
  const handleShowUserOptions = () => {
    setShowUserOptions((prev) => !prev);
  };
  const handleClickOutside = (e) => {
    const ref = myRef.current;
    const outside = e.target;

    if (ref && !ref.contains(outside)) {
      setShowUserOptions((prev) => !prev);
    }
  };
  useEffect(() => {
    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);
  return (
    <div className="userPanelContainer">
      <div
        ref={myRef}
        onClick={handleShowUserOptions}
        className="userPanelContainer-name"
      >
        <img src={"/img/svg/user-icon.svg"} alt="User Icon" />
        <span></span>
        <i className="fas fa-sort-down" />
      </div>
      {showUserOptions && (
        <UserOptions
          handleClickOutside={handleClickOutside}
          handleShowUserOptions={handleShowUserOptions}
        />
      )}
    </div>
  );
};

export default UserPanel;
