import * as adminActions from "../../actions/adminActions/constants";

const initialState = {
  options: {
    isLoading: true,
  },
  roles: {
    isLoading: true,
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case adminActions.PROFILE_GET_OPTIONS_SUCCESS:
      return {
        ...state,
        options: {
          ...action.payload,
          isLoading: false,
        },
      };
    case adminActions.ADMIN_GET_ROLES_SUCCESS:
      return {
        ...state,
        roles: {
          ...action.payload,
          isLoading: false,
        },
      };
    default:
      return state;
  }
};
