import * as platformReportsActions from "../../actions/platformReportsActions/constants";

const initialState = {
  trades: {
    data: [],
  },
  orders: {
    data: [],
  },
  withdrawals: {
    data: [],
  },
  deposits: {
    data: [],
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case platformReportsActions.PLATFORM_GET_TRADES_REPORTS_SUCCESS:
      return {
        ...state,
        trades: {
          ...action.payload,
        },
      };
    case platformReportsActions.PLATFORM_GET_ORDERS_REPORTS_SUCCESS:
      return {
        ...state,
        orders: {
          ...action.payload,
        },
      };
    case platformReportsActions.PLATFORM_GET_WITHDRAWALS_REPORTS_SUCCESS:
      return {
        ...state,
        withdrawals: {
          ...action.payload,
        },
      };
    case platformReportsActions.PLATFORM_GET_DEPOSITS_REPORTS_SUCCESS:
      return {
        ...state,
        deposits: {
          ...action.payload,
        },
      };
    default:
      return state;
  }
};
