import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import { userSignOutAction } from "../../store/actions/userActions";

const UserOptions = ({ handleClickOutside, handleShowUserOptions }) => {
  const dispatch = useDispatch();
  const [userOptionsClass, setUserOptionsClass] = useState("userOptions-hide");
  useEffect(() => {
    const clickListener = (e) => {
      handleClickOutside(e);
    };
    document.addEventListener("click", clickListener, false);
    setTimeout(() => {
      setUserOptionsClass("userOptions-show");
    }, 100);
    return () => {
      document.removeEventListener("click", clickListener, false);
    };
  }, [handleClickOutside]);
  return (
    <ul className={userOptionsClass}>
      <li
        className="userOptions-show--item"
        onClick={() => {
          dispatch(userSignOutAction());
          handleShowUserOptions();
        }}
      >
        Sign Out
      </li>
    </ul>
  );
};

UserOptions.propTypes = {
  handleClickOutside: PropTypes.any,
  handleShowUserOptions: PropTypes.any,
};

export default UserOptions;
