import * as actions from "../../actions/adminActions/constants";

const initialState = {
  bankName: "",
  bsb: "",
  accountName: "",
  accountNumber: "",
  accountHolder: "",
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actions.ADMIN_EDIT_BANK_DETAILS_SUCCESS:
      return {
        ...action.payload,
      };
    case actions.ADMIN_GET_BANK_DETAILS_SUCCESS:
      return {
        ...action.payload,
      };
    default:
      return state;
  }
};
