import { combineReducers } from "redux";
import { persistReducer } from "redux-persist";
import { i18nReducer } from "react-redux-i18n";
import storageSession from "redux-persist/lib/storage/session";

import user from "./userReducer";
import allClients from "./allClietsReducer";
import client from "./clientReducer";
import options from "./optionsReducer";
import me from "./meReducer";
import platformReports from "./platformReportsReducer";
import platformBalances from "./platformBalancesReducer";
import bankDetails from "./bankDetailsReducer";
import manualBanking from "./manualBankingReducer";

const rootPersistConfig = {
  key: "root",
  storage: storageSession,
  whitelist: ["user", "options"],
};

const rootReducer = combineReducers({
  user,
  allClients,
  client,
  options,
  me,
  platformReports,
  platformBalances,
  bankDetails,
  manualBanking,
  i18n: i18nReducer,
});

export default persistReducer(rootPersistConfig, rootReducer);
