import * as adminActions from "../../actions/adminActions/constants";

const initialState = {
  allClients: {
    isLoading: true,
  },
  allAdmins: {
    isLoading: true,
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case adminActions.ADMIN_GET_ALL_CLIENTS_SUCCESS:
      return {
        ...state,
        allClients: {
          ...action.payload,
          isLoading: false,
        },
      };
    case adminActions.ADMIN_GET_ALL_ADMINS_SUCCESS:
      return {
        ...state,
        allAdmins: {
          ...action.payload,
          isLoading: false,
        },
      };
    default:
      return state;
  }
};
