import React from "react";
import { URLS_LOCAL_GROUPS } from "../utils/config/URLS";

const SignIn = React.lazy(() => import("../components/SignIn"));
const ManualBanking = React.lazy(() =>
  import("../components/AdminCabinet/ManualBanking")
);
const Reports = React.lazy(() => import("../components/AdminCabinet/Reports"));
const Security = React.lazy(() =>
  import("../components/AdminCabinet/Security")
);
const MeManager = React.lazy(() =>
  import("../components/AdminCabinet/MeManager")
);
const PlatformBalances = React.lazy(() =>
  import("../components/AdminCabinet/PlatformBalances")
);
const AdminUsers = React.lazy(() =>
  import("../components/AdminCabinet/AdminUsers")
);
const ClientUsers = React.lazy(() =>
  import("../components/AdminCabinet/ClientUsers")
);
const ResetPassword = React.lazy(() => import("../components/ResetPassword"));
const SetNewPassword = React.lazy(() => import("../components/SetNewPassword"));
const UserProfile = React.lazy(() =>
  import("../components/AdminCabinet/ClientUsers/UserProfile")
);
const SetNewAdminPassword = React.lazy(() =>
  import("../components/SetNewAdminPassword")
);
const TwoFactorVerifyContainer = React.lazy(() =>
  import("../components/TwoFactorVerify")
);

export const routes = [
  {
    path: URLS_LOCAL_GROUPS.clientUser + "/:id?" + "/:type?" + "/:accType?",
    name: "Client user",
    element: <UserProfile />,
  },
  {
    path: URLS_LOCAL_GROUPS.clientUser,
    name: "Client user single",
    element: <ClientUsers />,
  },
  {
    path: URLS_LOCAL_GROUPS.adminUser,
    name: "Admin user",
    element: <AdminUsers />,
  },
  {
    path: URLS_LOCAL_GROUPS.platformBalances + "/:ccy",
    name: "Platform balance",
    element: <PlatformBalances />,
  },
  {
    path: URLS_LOCAL_GROUPS.meManager,
    name: "Manager",
    element: <MeManager />,
  },
  {
    path: URLS_LOCAL_GROUPS.security + "/:step?",
    name: "Security",
    element: <Security />,
  },
  {
    path: URLS_LOCAL_GROUPS.reports + "/:type?",
    name: "Reports",
    element: <Reports />,
  },
  {
    path: URLS_LOCAL_GROUPS.manual_banking + "/:type?" + "/:tab?",
    name: "Manual banking",
    element: <ManualBanking />,
  },
];

export const authRoutes = [
  { path: URLS_LOCAL_GROUPS.signIn, element: <SignIn /> },
  {
    path: URLS_LOCAL_GROUPS.activateAdminPassword,
    element: <SetNewAdminPassword />,
  },
  { path: URLS_LOCAL_GROUPS.two_factor, element: <TwoFactorVerifyContainer /> },
  { path: URLS_LOCAL_GROUPS.passwordReset, element: <SetNewPassword /> },
  {
    path: URLS_LOCAL_GROUPS.forgotPassword,
    element: <ResetPassword />,
  },
];
