import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { I18n } from "react-redux-i18n";
import "./Input.less";

const Input = (props) => {
  const [value, setValue] = useState("");
  useEffect(() => {
    const { defaultValue } = props;
    setValue(defaultValue);
  }, [props]);
  const handleFocus = (event) => {
    const { clearOnFocus, onChange } = props;
    if (clearOnFocus) {
      setValue("");
    }
    if (onChange) {
      onChange(event);
    }
  };
  const handleBlur = (event) => {
    const { onChange } = props;
    if (onChange) {
      onChange(event);
    }
  };
  const handleChange = (event) => {
    const { onChange } = props;
    setValue(event.target.value);
    if (onChange) {
      onChange(event);
    }
  };
  const {
    type,
    style,
    placeholder,
    name,
    title,
    step,
    min,
    id,
    max,
    className,
    disable,
    hidden,
    error,
    onKeyPress,
    onKeyDown,
    notTranslate,
    disableAutoComplete,
    additional,
    label,
    value: propValue,
    onClick,
  } = props;
  if (hidden) return <div className="input-wrap hidden" />;
  else {
    return (
      <div
        className={`${className} input-wrap${error ? " error" : ""}`}
        style={{ style }}
      >
        <label>
          {label}
          <input
            name={name}
            id={id}
            step={step}
            min={min}
            max={max}
            title={title}
            type={type || "text"}
            value={propValue !== undefined ? propValue : value}
            placeholder={notTranslate ? placeholder : I18n.t(placeholder)}
            disabled={disable}
            onFocus={handleFocus}
            onClick={onClick}
            onBlur={handleBlur}
            onChange={handleChange}
            onKeyPress={onKeyPress}
            onKeyDown={onKeyDown}
            autoComplete={disableAutoComplete ? "off" : "on"}
            {...additional}
          />
        </label>
        {error && (
          <div className="error-wrap" style={error ? {} : { display: "none" }}>
            <span className="error-item">{error}</span>
          </div>
        )}
      </div>
    );
  }
};

Input.propTypes = {
  type: PropTypes.string,
  title: PropTypes.string,
  style: PropTypes.object,
  placeholder: PropTypes.string,
  name: PropTypes.string,
  disable: PropTypes.bool,
  hidden: PropTypes.bool,
  onChange: PropTypes.func,
  defaultValue: PropTypes.any,
  step: PropTypes.any,
  min: PropTypes.any,
  id: PropTypes.any,
  max: PropTypes.any,
  className: PropTypes.any,
  additional: PropTypes.object,
  clearOnFocus: PropTypes.bool,
  label: PropTypes.string,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  onClick: PropTypes.func,
  onKeyPress: PropTypes.func,
  onKeyDown: PropTypes.func,
  notTranslate: PropTypes.bool,
  disableAutoComplete: PropTypes.bool,
  error: PropTypes.string,
};

Input.defaultProps = {
  type: "text",
  style: {},
  placeholder: "",
  name: "",
  title: "",
  disable: false,
  hidden: false,
  clearOnFocus: false,
  additional: {},
  onChange: () => {},
};

export default Input;
